.settings {
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    grid-template-columns: auto 1fr;
    column-gap: 1rem;
    justify-content: center;
    align-content: center;
    justify-items: center;
}

.settings_title {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
    width: 100%;
    grid-column: 1 / -1;
    justify-self: stretch;
    background-color: var(--primary);

    font-size: 2rem;
    font-weight: 700;

    border-top-right-radius: 1rem;
    border-top-left-radius: 1rem;

    margin-bottom: 1rem;

    transition: all 300ms;
}

.settings_title:hover {
    background-color: transparent;
    box-shadow: inset 0 0 0 3px var(--primary);
    color: var(--primary);
}

.settings_info h2 {
    text-align: center;
    font-size: 1.5rem;
}

.settings_info p {
    font-size: 1.5rem;
}

.settings_info {
    background-color: var(--grey-2);
    color: #fff;
    padding: 1rem 0.5rem;
    margin-bottom: 1rem;

    transition: all 300ms;
}

.settings_info:hover {
    background-color: transparent;
    box-shadow: inset 0 0 0 3px var(--primary);
    color: var(--primary);
}

.close_button {
    grid-column: 1 / -1;
    justify-self: stretch;
    background-color: var(--primary);
    border: none;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
    font-size: 2rem;
    font-weight: 600;

    transition: all 300ms;
}

.close_button:hover {
    background-color: transparent;
    box-shadow: inset 0 0 0 3px var(--primary);
    color: var(--primary);
}

.dropdown {
    margin-bottom: 1rem;
}
