.current_users {
    display: flex;
    align-items: center;
    gap: 1.6rem;
    margin-top: 8rem;
}

.current_users_imgs {
    display: flex;
}

.current_users_imgs img {
    height: 4.8rem;
    width: 4.8rem;
    border-radius: 50%;
    margin-right: -1.6rem;
    border: 3px solid var(--tint-1);
}

.current_users_imgs img:last-child {
    margin: 0;
}

.current_users_text {
    display: flex;
    align-items: center;
    align-self: flex-end;
    font-size: 1.8rem;
    font-weight: 600;
}

.current_users_text span {
    color: #fff;
    font-weight: 700;
    font-size: 2.5rem;
}
